
.homepage{
	width:100%;
    height:80%;
    margin-top:0; 
	background-color: #010e13;
	
   /** animation: backgroundColorChnager 10s infinite alternate linear;*/
    
}
.headLine{
	background-color: #02151c;
	padding-bottom: 1%;
}

.headLine h1{
	font-size: 50px;
	color: white;
	
}
.headLine h2,h3 {
	color: white;
}
/* component*/
.components{
	width: 100%;
	display:flex;
}

.tech{
	width: 39%;
	float: left;
	margin-left:1%;
	text-align: justify;
    text-justify: inter-word;
	padding-left: 1%;
	box-shadow: inset 0 3px 6px rgba(232, 227, 227, 0.16), 0 4px 6px rgba(216, 202, 202, 0.45);
  	border-radius: 10px;
	
}
.tech h1{
	color:white;
}

.tech h4, h5{
	display: flex;
	display: inline;
	color: white;
	
}
.tech h4{
	font-size: 15px;
	
}
.tech h5{
	color:#f8f8f8
	
}
/*projects*/
.project{
	float: left;
	margin:0;
	width: 39%;
	text-align: justify;
    text-justify: inter-word;
	padding-left: 1%;
	box-shadow: inset 0 3px 6px rgba(232, 227, 227, 0.16), 0 4px 6px rgba(216, 202, 202, 0.45);
  	border-radius: 10px;


}
.project h1{
	color: white;
}
.listOfProjects{
	margin: 0;
	display: inline-flex;
}
.listOfProjects h4{
	color: white;
}
.listOfProjects a{
	color:grey;
}


/*conacts*/
.contact{
	float: left;
	margin-right: 1%;
	width: 20%;
	text-align: justify;
    text-justify: inter-word;
	padding-left: 1%;
	box-shadow: inset 0 3px 6px rgba(232, 227, 227, 0.16), 0 4px 6px rgba(216, 202, 202, 0.45);
  	border-radius: 10px;
}
.contact h1{
	color: white;
}

.social-icons {
	text-align: justify;
    text-justify: inter-word;
	display: inline-block;
    size: 12px; 
    
  }
  .social-icons ul li{
	margin: 10%;
  } 
  .social-icons a {
    color: gray;
    font-size: 5px;
	display: inline-block; 
  }

  .social-icons a:hover {
    color: white;
  }


